import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import {FormsModule} from '@angular/forms';
import {MatDialogModule,MatFormFieldModule ,MatInputModule} from '@angular/material';
import {InlineEditComponent} from './inline-edit/inline-edit.component';
import { SelectOnClickDirective } from './select-on-click.directive';
import { NgmDialogComponent } from './ngm-dialog/ngm-dialog.component';

@NgModule({
  imports: [
    CommonModule,
    FormsModule,
    MatDialogModule,
    MatFormFieldModule,
    MatInputModule
  ],
  declarations: [InlineEditComponent, SelectOnClickDirective, NgmDialogComponent],
  exports:[InlineEditComponent,SelectOnClickDirective, NgmDialogComponent]
})
export class SharedModule { }



