import { Component, OnInit, ViewChild, ElementRef } from '@angular/core';
import { NgForm } from '@angular/forms';
import { MatDialog, MatDialogRef } from '@angular/material';

import { CsvService } from '../../../services/csv.service';
import { PagerService } from '../../../services/pager.service';

import { Observable } from 'rxjs/Observable';
import 'rxjs/add/observable/of';
import 'rxjs/add/operator/map';
import * as csv from 'csv';
import * as _ from 'lodash';
import { PapaParseService } from 'ngx-papaparse';
import * as fileSaver from 'file-saver'
import { NgmDialogComponent } from '../../../shared/ngm-dialog/ngm-dialog.component';

@Component({
  selector: 'app-manage-csv',
  templateUrl: './manage-csv.component.html',
  styleUrls: ['./manage-csv.component.css']
})
export class ManageCsvComponent implements OnInit {
  @ViewChild('fileInput') fileInput: ElementRef;
  @ViewChild('selectCsvFile') selectCsvFile: ElementRef;

  isDataFetched: boolean = false;
  isFetching: boolean = false;
  isColumnFilter: boolean = false;
  isCsvFileSelected: boolean;

  workingFile: any;
  csvError: string = "";
  sucessMsg: string = "";

  csvFiles = [];
  csvData: any = [];
  csvHeader: any = [];
  copiedItems: any = [];
  selectedItems: any = [];
  addedItems: any = [];
  copyAddItems: any = [];
  csvDatabackUp: any = [];

  listFilter: any;
  filteredCsvData: any;

  rowNumber: any;
  selectedRow: any;

  selectedCsvFile: string;
  headerSearchData: any = [];

  // pager object
  pager: any = {};
  // paged items
  pagedItems: any[];

  customTrackBy(index: number, obj: any): any {
    return index;
  }

  isSelectedItem(csv: any) {
    if (this.selectedItems.length > 0) {
      return this.selectedItems.lastIndexOf(csv) != -1 ? 'true' : 'false';
    }
    else {
      return 'false';
    }
  }

  isAddedItem(csv: any) {
    if (this.addedItems.length > 0) {
      let ai = this.addedItems.indexOf(csv);
      if (ai != -1) {
        let itemIndx = this.filteredCsvData.indexOf(csv);
        let lastIndx = this.filteredCsvData.lastIndexOf(csv);
        return lastIndx == itemIndx ? 'true' : 'false';
      }
    }
    else {
      return 'false';
    }
  }

  constructor(private csvService: CsvService, private papa: PapaParseService, private pagerService: PagerService, public dialog: MatDialog) {
    this.selectedCsvFile = "default";
  }

  ngOnInit() {
    this.csvService.getCsvList().subscribe(
      data => {
        this.csvFiles = data;
      },
      err => {
        err => console.log('error', err)
      }
    )
  }

  toogleColumnFilter() {
    //create header model object;
    this.headerSearchData = Object.assign([], this.csvHeader);
    this.isColumnFilter = !this.isColumnFilter;
  }

  multiColumnFilter() {
    let filter = this.headerSearchData;
    for (let i = 0; i < filter.length; i++) {
      if (filter[i] != this.csvHeader[i]) {
        this.filteredCsvData = this.filteredCsvData.filter(function (el) {
          let searchValue = _.trim(filter[i].toLowerCase());
          if (el[i].toLowerCase().indexOf(searchValue) != -1) {
            return el;
          }
        });
      }
    }
  }

  getFilteredList() {    
    this.multiColumnFilter();
    if (this.csvData.length != this.filteredCsvData.length) {
      this.setPage(1);
    }
  }

  onHeaderBlur(event: any, colIndex: number, header: string) {
    this.headerSearchData[colIndex] = event.srcElement.value;
    this.getFilteredList();
  }
   
  validateCsvFileSelected() {
    if (this.selectedCsvFile === "default") {
      console.log(this.selectedCsvFile);
      this.isCsvFileSelected = false;
    } else {
      this.isCsvFileSelected = true;
    }
    return this.isCsvFileSelected;
  }

  resetLayoutArrays() {
    this.csvError = "";
    this.sucessMsg = "";
    this.addedItems.length = 0;
    this.selectedItems.length = 0;
  }

  uploadFile(event: any) {
    var kt = this.fileInput;
    this.resetLayoutArrays();
    let fileList = event.target.files;
    if (fileList && fileList.length > 0) {
      this.isCsvFileSelected = true;
      this.isFetching = true;
      var k = fileList[0];
      this.selectedCsvFile = "default";
      var reader = new FileReader();
      let s = reader.readAsText(k);
      this.workingFile = fileList[0].name;
      reader.onloadend = (e) => {
        this.papa.parse(reader.result, {
          skipEmptyLines: true,
          complete: (results, file) => {
            this.setCsvForEditing(results.data);
            this.isFetching = false;
            this.isDataFetched = true;
          }
        },
        );
      };
    }
  }

  getIndex(curIndex): number {
    let indx;
    if (this.pager.currentPage > 1) {
      indx = (this.pager.currentPage - 1) * this.pager.pageSize + curIndex;
    }
    else {
      indx = curIndex;
    }
    return indx;
  }

  copyRow(index: number) {
    console.log(index);
    let actualIndex = this.getIndex(index);
    if (this.copiedItems.indexOf(this.filteredCsvData[actualIndex]) == -1) {
      this.copiedItems.push(this.filteredCsvData[actualIndex]);
    }
  }

  add() {
    let itemsToAdd, itemsAddAt;
    if (this.copiedItems.length > 0) {
      //to add after copied item
      itemsAddAt = this.filteredCsvData.indexOf(this.copiedItems[0]) + 1;
      itemsToAdd = this.copiedItems.length;

    } else {
      itemsAddAt = -1;
      this.copiedItems = this.csvData.slice(-1);
      itemsToAdd = 1;
    }

    if (itemsToAdd > 0) {
      for (let ci = 0; ci < this.copiedItems.length; ci++) {
        let clone = Object.assign([], this.copiedItems[ci]);
        if (this.addedItems.indexOf(clone) == -1) {
          this.addedItems.push(clone);
        }
        this.filteredCsvData.splice(itemsAddAt + ci, 0, clone);
      }
    }
    this.setPage(1);
  }

  copyAndAddRow(event: any, index: number) {
    this.addedItems.length=0;
    this.copiedItems.length = 0;
    this.copyRow(index);
    this.add();
    event.stopPropagation();
  }

  openDialog(event: any, index: number): void {
    console.log('Dialog Opened;');
    let dialogRef = this.dialog.open(NgmDialogComponent, {
      width: '300px',
      data: {
        title: 'Alert : ' + this.workingFile, message: "Are you sure want to delete?"
        , btnConfirmText: 'Confirm', btnCancelText: 'Cancel'
      }
    });

    dialogRef.afterClosed().subscribe(result => {
      if(result === true){
        this.delete(event, index);
      }
      else{
        console.log('Do nothing');
      }
    });
  }

  delete(event: any, index: number) {
    let actualIndex = this.getIndex(index);
    this.filteredCsvData.splice(index, 1);
    this.setPage(1);
    console.log('Row :'+index+'deleted.');
    event.stopPropagation();
  }

  download() {
    //let fileToSave = this.workingFile + '-'+(new Date()).toISOString()+'.csv';
    var csv = this.papa.unparse({
      fields: this.csvHeader,
      data: this.csvData
    });
    let blob = new Blob([csv], { type: 'text/plain' });
    fileSaver.saveAs(blob, this.workingFile)
  }

  loadFile(filename: string) {
    this.isFetching = true;
    this.resetLayoutArrays();
    if (this.validateCsvFileSelected()) {
      this.fileInput.nativeElement.value = "";
      this.workingFile = this.selectedCsvFile;
      this.csvService.getCsvData(filename).subscribe(
        data => {
          this.setCsvForEditing(data);
          this.isFetching = false;
          this.isDataFetched = true;
        },
        err => {
          console.log('error', err);
          this.isFetching = false;
          this.isDataFetched = false;
          this.csvError = err;
        }
      );
    }
    else {
      console.log(this.selectedCsvFile);
    }
  }

  setCsvForEditing(data: any) {
    this.csvDatabackUp = Object.assign([], data);
    this.csvData = data;
    this.csvHeader = this.csvData.shift();
    this.filteredCsvData = this.csvData;
    // initialize to page 1
    this.setPage(1);
  }

  setPage(page: number) {
    // get pager object from service
    if (this.filteredCsvData.length >0) {
      this.sucessMsg = '';
      this.pager = this.pagerService.getPager(this.filteredCsvData.length, page);
      if (page < 1 || page > this.pager.totalPages) {
        return;
      }
      // get current page of items
      this.pagedItems = this.filteredCsvData.slice(this.pager.startIndex, this.pager.endIndex + 1);
    }else{
      this.sucessMsg = 'No Records';
      this.pagedItems = this.filteredCsvData;
    }
  }

  search() {
    let filter = this.listFilter;
    //clear column filter
    this.headerSearchData = this.csvHeader;
    this.isColumnFilter = false;

    if (filter) {
      var k = this.csvData.filter(function (el) {
        return !!~el.indexOf(filter);
      });
      this.filteredCsvData = k;
    } else {
      this.filteredCsvData = this.csvData;
    }
    this.setPage(1);
  }

  reset() {
    this.fileInput.nativeElement.value = "";
    this.selectedCsvFile = "default";
    this.selectedItems.length = 0;
    this.copiedItems.length = 0;
    this.csvData.length = 0;
    this.csvHeader.length = 0;
    this.csvDatabackUp.length = 0;
    this.headerSearchData.length = 0;
    this.filteredCsvData.length = 0;
    this.pagedItems.length = 0;
    this.setPage(1);
    this.isDataFetched = false;
  }

  postCsv(form: NgForm) {
    console.log('Post CSV Clicked');
    //Add header to CSV data
    let cloneData = Object.assign([], this.csvData);
    cloneData.unshift(this.csvHeader);
    //let dataTmp = this.csvData.slice();
    //dataTmp.unshift(this.csvHeader);

    this.csvService.postCsvFile(cloneData, this.selectedCsvFile).subscribe(
      data => {
        console.log('success', data);
        this.sucessMsg = data.poststatus;
        this.reset();
      },
      err => {
        console.log('error', err);
        this.csvError = err;
      }
    );
  }
}


