import { BrowserModule } from '@angular/platform-browser';
import { BrowserAnimationsModule } from '@angular/platform-browser/animations';
import { NgModule } from '@angular/core';

import { AppComponent } from './app.component';
import { ContentBuilderModule } from './content-builder/content-builder.module';
import { ContentBuilderComponent } from './content-builder/content-builder.component';
import { HeaderWithFilterComponent } from './shared/header-with-filter/header-with-filter.component';
import {MatToolbarModule,MatIconModule,MatMenuModule,MatDialogModule,MatFormFieldModule,MatInputModule,
        MatTableModule,MatCardModule }from '@angular/material';

@NgModule({
  declarations: [
    AppComponent,
    HeaderWithFilterComponent
  ],
  imports: [
    BrowserModule,   
    BrowserAnimationsModule,
    MatToolbarModule,MatIconModule,MatMenuModule,MatDialogModule,MatFormFieldModule,MatInputModule,
    MatTableModule,MatCardModule,
    ContentBuilderModule
],
  providers: [],
  bootstrap: [AppComponent,ContentBuilderComponent]
})
export class AppModule { }



