import { Component, OnInit, Inject } from '@angular/core';
import { MatDialog, MatDialogRef, MAT_DIALOG_DATA } from '@angular/material';
import { DialogData } from '../../interfaces/dialog-data';
@Component({
  selector: 'app-ngm-dialog',
  templateUrl: './ngm-dialog.component.html',
  styleUrls: ['./ngm-dialog.component.css']
})
export class NgmDialogComponent implements OnInit {

  constructor(public dialogRef: MatDialogRef<NgmDialogComponent>,
              @Inject(MAT_DIALOG_DATA) public data: DialogData) { 
  }

  onNoClick(): void {
    this.dialogRef.close();
  }

  onConfirmClick():void{
    this.dialogRef.close(true);
  }
  ngOnInit() {
    
  }

}



