import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { FormsModule } from '@angular/forms';


import { ManageCsvComponent } from './manage-csv/manage-csv.component';
import { CsvService } from '../../services/csv.service';
import { PagerService } from '../../services/pager.service';
import { InlineEditComponent } from '../../shared/inline-edit/inline-edit.component';
import { MatCsvComponent } from './mat-csv/mat-csv.component';
import {SharedModule} from './../../shared/shared.module';
import {PapaParseModule} from 'ngx-papaparse';
import { NgmDialogComponent } from '../../shared/ngm-dialog/ngm-dialog.component';

@NgModule({
  imports: [
    CommonModule,
    FormsModule,
    SharedModule,
    PapaParseModule
  ],
  declarations: [ManageCsvComponent,MatCsvComponent],
  providers: [CsvService,PagerService],
  exports: [ManageCsvComponent, MatCsvComponent],
  entryComponents: [ManageCsvComponent, NgmDialogComponent],
})
export class CsvManagerModule { }



