import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { ContentBuilderComponent } from './content-builder.component';
import { CsvManagerModule } from './csv-manager/csv-manager.module';
import { SharedModule } from '../shared/shared.module';
import  { HttpModule}  from '@angular/http';
import { FormsModule } from '@angular/forms';


@NgModule({
  imports: [
    CommonModule,CsvManagerModule, HttpModule,FormsModule,SharedModule
  ],
  declarations: [ContentBuilderComponent],
  exports: [ContentBuilderComponent]  
})
export class ContentBuilderModule { }



