import { Injectable } from '@angular/core';
import { Http, Response, Headers, RequestOptions } from '@angular/http';
import 'rxjs/Rx';
import { Observable } from "rxjs/Observable";

@Injectable()
export class CsvService {
  public apiUrl: string = 'http://localhost:3002/api/';
  constructor(private http: Http) {

  }

  setHeader() {
    let headers = new Headers({
      'content-type': 'application/json',
      'Access-Control-Allow-Origin': '*',
      'Access-Control-Allow-Headers': 'Origin,X-Requested-With,Content-Type,Accept'
    });
    // Request methods you wish to allow
    headers.append('Access-Control-Allow-Methods', 'GET, POST, OPTIONS, PUT, PATCH, DELETE');

    return headers;
  }

  private extractData(res: Response) {
    let body = res.json();

    return body || {};
  }

  private csvData(res: Response) {
    let body = res.json();

    return body || {};
  }

  private handleError(error: any) {
    console.log('Error : ' + error);
    return Observable.throw(error._body);
  }

  postCsvFile(dataJson: any,fileName:string) {
    let body = dataJson;
    let requestOptions = new RequestOptions({ headers: this.setHeader() });
    console.log("Post CSV operation Called : ");
    
    return this.http.post(this.apiUrl + 'post-csv/'+fileName, body, requestOptions)
      .map(this.extractData)
      .catch(this.handleError);
  }

  getCsvData(fileName: string): Observable<any> {
    let requestOptions = new RequestOptions({ headers: this.setHeader()});

    return this.http.get(this.apiUrl + 'get-csv/'+fileName, requestOptions)
      .map(this.csvData)
      .catch(this.handleError);
  }

  getCsvList(): Observable<any> {
    let requestOptions = new RequestOptions({ headers: this.setHeader() });
    return this.http.get(this.apiUrl + 'csv-list', requestOptions)
      .map(this.csvData)
      .catch(this.handleError);
  }
}



